.social-icons {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  bottom: 0;
  left: 3rem;
  z-index: 3;
}

.social-icon {
  margin: 5px;
}

.social-icon a {
  color: #3f5168;
}

.social-icon a:hover {
  color: var(--accent);
}

.social-icons-line {
  width: 2px;
  height: 8rem;
  margin-top: 10px;
  background-color: #3f5168;
}